<template>
  <v-container >
    <!-- Tasks to Review -->
    <v-row >
      <v-col cols="12">
        <v-list dense>
          <v-list-item
            v-for="task in filteredTasks"
            :key="task.taskId"
            class="task-item"
          >
            <!-- Task Summary -->
            <v-list-item-content @click="$emit('update:isEditingTask',task.taskId)">
              <v-list-item-title>
                <strong>{{ task.title }}</strong>
              </v-list-item-title>
              <v-list-item-subtitle>
                <p><strong>Ends In:</strong> {{ getRemainingTime(task) }}</p>
                <v-chip
                  :color="getPriorityColor(task.priority)"
                  class="priority-chip"
                  small
                >
                  {{ task.priority }}
                </v-chip>
              </v-list-item-subtitle>
              <v-list-item-subtitle>
              <p><strong>Estimated Time:</strong> {{ task.estimatedHours }} hours</p>
              </v-list-item-subtitle>
              <v-list-item-subtitle>
              <p><strong>Min Bid:</strong> {{ getMinimumBid(task) }} hours</p>
              </v-list-item-subtitle>
                <!-- Star Rating -->
            </v-list-item-content>
            <v-btn
              icon
              @click="toggleTask(task.taskId)"
            >
              <v-icon>{{ isTaskExpanded(task.taskId) ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>

            <!-- Expanded Task Details -->
            <v-expand-transition>
              <div v-show="isTaskExpanded(task.taskId)" class="task-details">
                <p><strong>Created By:</strong> {{ getUserName(task.createdBy) }}</p>
                <p><strong>Deadline:</strong> {{ formatDate(task.deadline) }}</p>
                <p><strong>Description:</strong> {{ task.description }}</p>
                


                <!-- Comments Section -->
                <v-btn
                  text
                  small
                  color="primary"
                  @click="toggleComments(task.taskId)"
                >
                  {{ areCommentsExpanded(task.taskId) ? 'Hide Comments' : 'Show Comments' }}
                </v-btn>
                <v-expand-transition>
                  <div v-show="areCommentsExpanded(task.taskId)" class="comments-section">
                    <v-list dense>
                      <v-list-item
                        v-for="comment in task.comments"
                        :key="comment.commentId"
                      >
                        <v-list-item-content>
                          <p>
                            <strong>{{ getUserName(comment.author) }}</strong>:
                            {{ comment.content }}
                          </p>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list>
                  </div>
                </v-expand-transition>
                <div align="center" class="mt-3">

                  <v-row justify="center" class="mt-5">
                    <v-col cols="12" md="8">
                      <v-card class="pa-3">
                        <v-card-title>
                          <span class="text-h6">Bids for Task: {{ task.title }}</span>
                        </v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                          <!-- Bids List -->
                          <v-list dense>
                            <v-list-item
                              v-for="bid in sortedBids(task)"
                              :key="bid.userId"
                              class="bid-item"
                            >
                              <div>
                                <v-list-item-title>
                                  <strong>{{ getUserName(bid.userId) }}</strong>
                                </v-list-item-title>
                                <v-list-item-subtitle>
                                  <strong>Bid Hours:</strong> {{ bid.bidHours }} hours
                                </v-list-item-subtitle>
                              </div>
                            </v-list-item>
                          </v-list>

                          <!-- Add New Bid -->
                          <v-divider class="my-4"></v-divider>
                          <h4 class="text-h6">Place Your Bid</h4>
                          <v-form ref="bidForm" v-model="bidFormValid">
                            <v-text-field
                              v-model.number="newBidHours"
                              label="Enter Bid Hours"
                              type="number"
                              min="1"
                              :rules="[rules.required, rules.positive]"
                              dense
                              outlined
                              class="mb-3"
                            ></v-text-field>
                            <v-btn
                              color="primary"
                              block
                              @click="placeBid(task)"
                            >
                              Place Bid
                            </v-btn>
                          </v-form>
                        </v-card-text>
                      </v-card>
                    </v-col>
                  </v-row>
                </div>

              </div>
            </v-expand-transition>
          </v-list-item>
        </v-list>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "TaskReview",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      newBidHours: null,
      bidFormValid: false,
      rules: {
        required: (value) => !!value || "This field is required",
        positive: (value) => value > 0 || "Value must be greater than 0",
      },
      expandedTasks: {}, // Tracks expanded tasks
      expandedComments: {}, // Tracks expanded comments for each task
    };
  },
  mounted(){
    this.updateAuction();
  },
  computed: {
    filteredTasks() {
      return this.tasks.filter(
        (task) => task.type === "Auctioned" && !task.auction.isAuctioned
      );
    },
  },
  methods: {
    sortedBids(task) {
      if (!task || !task.auction) {
        return [];
      }
      return [...task.auction.bids].sort(
        (a, b) => a.bidHours - b.bidHours
      );
    },
    getUserName(userId) {
      const user = this.users.find((u) => u.userId === userId);
      return user ? user.username : "Unknown User";
    },
    getPriorityColor(priority) {
      const colors = {
        High: "red",
        Medium: "orange",
        Low: "green",
        None: "gray",
      };
      return colors[priority] || "gray";
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString(undefined, {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
      });
    },
    toggleTask(taskId) {
      this.expandedTasks[taskId] = !this.expandedTasks[taskId];
    },
    isTaskExpanded(taskId) {
      return !!this.expandedTasks[taskId];
    },
    toggleComments(taskId) {
      this.expandedComments[taskId] = !this.expandedComments[taskId];
    },
    areCommentsExpanded(taskId) {
      return !!this.expandedComments[taskId];
    },
    getMinimumBid(task) {
      if (!task.auction || task.auction.bids.length === 0) {
        return "No bids";
      }
      return Math.min(...task.auction.bids.map((bid) => bid.bidHours));
    },
    getRemainingTime(task) {
      const endDate = new Date(task.auction.deadline);
      const now = new Date();

      const diff = endDate - now;
      if (diff <= 0) {
        return "Expired";
      }

      const days = Math.floor(diff / (1000*60*60*24));
      const hours = Math.floor(diff % (1000 * 60 * 60*24)/(1000*60*60));
      const minutes = Math.floor((diff % (1000 * 60 * 60) / (1000*60)));
      return `${days}d ${hours}h ${minutes}m`;
    },
    placeBid(task) {
      if (!this.bidFormValid || !task) return;

      let data = {
        task: task,
        bid: {
            userId: this.loginUser.userId, // Replace with actual user ID
            bidHours: this.newBidHours,
        },
      };

      this.$emit("update:networkRequests", 'place-bid', data);

      this.newBidHours = null;
    },
    updateAuction(){
      let currentDate = new Date;
      currentDate = currentDate.toISOString();
      for(let task of this.tasks){
        console.log("Updating Tasks",task.title, currentDate, task.auction.deadline);
        if(currentDate > task.auction.deadline){
          this.$emit("update:networkRequests", 'task-auction-update', task);
        }
      }
    }
  },
};
</script>
<style scoped>
.task-item {
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 10px;
}

.task-details {
  padding: 10px;
  background: #f9f9f9;
  border-radius: 5px;
}

.comments-section {
  padding: 10px;
  background: #f0f0f0;
  border-radius: 5px;
  margin-top: 10px;
}

.priority-chip {
  font-weight: bold;
  color: white;
}

.star-icon {
  cursor: pointer;
  font-size: 24px;
  margin-right: 4px;
}

.bid-item {
  padding: 8px 0;
}

.bid-item:not(:last-child) {
  border-bottom: 1px solid #e0e0e0;
}

.text-primary {
  color: #1976d2;
}
</style>
